* {
    transition: .3s;
}

ul {
    padding: 0;
    margin: 0;
}

a {
    text-decoration: none;
    color: inherit;
}

.App {
    padding: 30px 90px;    

    height: 100vh;
    box-sizing: border-box;

    font-family: 'Montserrat', sans-serif !important;

    background: var(--bg);
    transition: .5s;

    overflow: hidden;
}

.h100 {
    height: 100%;
    overflow: hidden;
}

.button {
    padding: 13px 25px;
    border-radius: 100px;

    font-size: 11px;
    font-weight: 500;
    
    border: 0;
    cursor: pointer;
    outline: none;
}

.input {
    padding: 0 20px;
    width: 100%;
    /* display: grid;
    justify-content: start; */
    border-radius: 100px;
    background: #fff;

    height: 35px;

    box-sizing: border-box;

    display: grid;
    grid-template-areas: 'input icon';
    grid-template-columns: 1fr 0px;
}

.input input {
    font-size: 12px;
    font-family: 'Montserrat', sans-serif !important;
    font-weight: 500;

    outline: none;

    padding: 10px 0px;   

    color: var(--text);

    width: 100%;
    box-sizing: border-box;
}

.input-search {
    padding: 0px 35px;

    display: grid;
    grid-template-areas: 'glass input icon';
    grid-template-columns: 0px 1fr 0px;
}

.input-search-button {
    height: 33px;
    width: 33px;
    border-radius: 50%;
    background: var(--color-1);
    transform: translateY(1px);

    display: flex;
    align-items: center;
    justify-content: center;

    cursor: pointer;
}

.input .input-search-glass {
    /* position: relative;
    left: 15px;
    top: 28px; */
    transform: translate(-20px, 12px);
}

.input .input-arrow-down {
    cursor: pointer;
    width: 10px;

    transform: translate(-5px, 16px);
}

.input-search .input-arrow-down {
    transform: translate(9px, 16px);

    cursor: pointer;
    width: 10px;
}



::-webkit-input-placeholder {
    font-family: 'Montserrat', sans-serif !important;
    font-weight: 500;
    color: var(--headers);
}



.checkbox .chbox {
    width: 17px;
    height: 17px;
    border: 1px solid silver;
    border-radius: 6px;
    background: #fff;
    display: block;
    cursor: pointer;
}

.checkbox .text-label {
    font-size: 12px;
    font-weight: 500;
    color: var(--color-2);

    margin-left: 7px;
}

.checkbox input {
    display: none;  
}

.checkbox {
    display: flex;
    align-items: center;
}

.checkbox img {
    display: none;
    position: absolute;
    transform: translate(5px, 0px);
}

.buy-form {
    height: 100vh;
    width: 100vw;
    background: rgba(0, 0, 0, 0.5);
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;

    display: none;
    align-items: center;
    justify-content: center;
}

.buy-form-content {
    width: 200px;
    /* height: 300px; */

    background: #ececec;

    display: grid;
    grid-template-areas: 'header' 'name' 'number' 'button';
    grid-template-rows: 50px 35px 35px 60px;

    grid-row-gap: 20px;

    padding: 10px 40px;
    border-radius: 30px;
}

.buy-form-name-input input {
    width: 130px;
    height: 35px;
}

.buy-form-number-input input {
    width: 130px;
    height: 35px;
}

.buy-form-content h3 {
    font-weight: 600;
    width: 100%;
    text-align: center;
}

.buy-form-content button {
    height: 38px;
    width: 120px;

    border-radius: 100px;
    border: none;
    background: #000;
    color: #fff;
    font-weight: 500;
    font-size: 10px;

    cursor: pointer;

    font-family: 'Montserrat', sans-serif;

    padding: 10px;   

    position: relative;
    left: 50%;
    transform: translateX(-50%);
    outline: none;
}

.buy-form-close-button {
    width: 80px;
    height: 80px;   
    position: absolute;
    right: 0;
    top: 0;

    display: flex;
    align-items: center;
    justify-content: center;
}

.buy-form-close-button img {
    height: 30px;

    cursor: pointer;
}


::-webkit-scrollbar { width: 4px; height: 3px;}
/* ::-webkit-scrollbar-button {  background-color: #666; } */
::-webkit-scrollbar-track {  background-color: #999;}
::-webkit-scrollbar-track-piece { background-color: #ffffff;}
::-webkit-scrollbar-thumb { height: 50px; background-color: var(--color-1); border-radius: 3px;}
::-webkit-scrollbar-corner { background-color: #999;}
::-webkit-resizer { background-color: var(--color-1);}