.catalog {
    height: calc(100% - 51px);
    display: grid;
    grid-template-areas: 'content' 'footer';
    grid-template-rows: auto 1fr;
}

.catalog-content {
    margin-top: 50px;

    display: grid;
    grid-template-areas: 'filters cards';
    grid-template-columns: 200px 1fr;

    grid-gap: 60px;
}

.filters {
}

.filters-search {
    margin-bottom: 28px;
}

.filters-mark {
    margin-bottom: 18px;
}

.filters h2 {
    font-size: 14px;
    font-weight: 500;

    color: var(--headers);

    margin-bottom: 15px;
}

.filters .drive {
    margin-top: 28px;
}

.filters .checkboxes {
    display: grid;
    grid-template-areas: "1 2" "3 4";
    grid-row-gap: 9px;
}

.filters .engine {
    margin-top: 28px;
}

.cars {
    display: flex;
    flex-wrap: wrap;
    overflow-y: auto;
    height: 450px;
}

.cars .card {
    margin-right: 30px;
    margin-bottom: 30px;

    background: #fff;

    display: grid;
    grid-template-areas: 
    "props"
    "img"
    "name"
    "colors"
    "price";    

    grid-template-rows: auto auto 19px auto 1fr;
    grid-template-columns: 269px;

    /* grid-gap: 7px; */

    justify-content: center;

    height: 214px;
    width: 269px;

    padding: 20px 0;

    border-radius: 30px;
    box-sizing: border-box;
}

.cars .card .car-props {
    font-size: 12px;
    font-weight: 500;
    
    width: 269px;
    text-align: center;
}

.cars .card .car-img {
    width: 180px;
    height: 88px;

    border: 0px solid silver;
}

.card .colors {
    margin-top: 7px;

    display: grid;
    justify-content: center;
}

.card .colors ul li {
    display: inline-block;
    padding: 0 7px;
}

.card .color {
    width: 9px;
    height: 9px;

    border: 0;

    box-sizing: border-box;
}

.card .car-name {
    margin-top: 15px;

    font-size: 14px;
    font-weight: 600;
    text-align: center;
}

.card .car-price {
    font-size: 12px;
    font-weight: 500;

    display: grid;
    align-items: end;
    
    text-align: center;
}

.search-results {
    font-weight: 600;
    color: var(--headers);
}