.details {
   height: 100%;
}

.details-content {
    height: calc(100% - 70px);

    margin-top: 50px;

    display: grid;
    grid-template-areas: 'similar car offers';
    grid-template-columns: 220px 1fr 230px;

    grid-gap: 60px;
}

.similar h1 {
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 1px;
    color: var(--headers);

    margin-top: 0px;
    margin-bottom: 25px;
}

.similar-cards {
    height: 460px;
    overflow: hidden;
}

.similar-cards:hover {
    overflow: auto;
}

.similar-cards .mini-card {
    display: grid;
    grid-template-areas: "price" "img" "name";
    grid-template-rows: auto 1fr auto;

    border-radius: 20px;

    padding: 16px 0;

    background: #fff;

    margin-bottom: 20px;

    width: 202px;
    height: 139px;
    box-sizing: border-box;
}

.mc-price {
    font-size: 12px;
    font-weight: 500;
    width: 100%;
    text-align: center;
}

.mc-name {
    font-size: 11px;
    font-weight: 600;

    width: 100%;
    text-align: center;

    color: var(--headers);
}

.mc-img {
    height: auto;
}

/* CURRENT CAR */

.current-car {
    display: grid;
    grid-template-areas: 
    "arrow"
    "img"
    "name"
    "colors"
    "props";
    grid-template-rows: 20px 1fr auto auto 160px;

    height: 100%;
}

.current-car .car-img {
    width: 600px;
}

.cc-car-name {
    font-size: 40px;
    font-weight: 700;
    color: var(--headers);

    text-align: center;
    letter-spacing: 2px;
}

.cc-properties {
    margin-top: 20px;

    /* display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr; */

    display: flex;
    flex-wrap: wrap;

    justify-content: space-between;
    align-items: end;
}

.cc-prop h3 {
    font-size: 16px;
    font-weight: 600;
    
    margin-bottom: 10px;
    
    color: var(--headers);
}

.cc-prop span {
    font-size: 14px;
}

.cc-prop-content {
    width: auto;
}

.current-car .color-picker {
    margin-top: 10px;
}

.back-button {
    display: flex;
    align-items: center;

    cursor: pointer;
}

.back-button img {
    transform: translateY(-2px);
}

.back-button span {
    font-size: 14px;
    font-weight: 500;

    margin-left: 16px;

    color: #535353;
}

/* OFFERS */

.offers-cards {
    height: 350px;
    overflow-y: auto;
    overflow-x: hidden;
}

.offers h1 {
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 1px;
    color: var(--headers);

    margin-top: 0px;
    margin-bottom: 25px;
}

.offer-card {
    display: grid;
    grid-template-areas: 'colors' 'options' 'p-l' 'check';
    grid-template-rows: auto auto 1fr 0;

    background: #fff;
    border-radius: 20px;

    padding: 15px 20px;
    box-sizing: border-box;

    height: 95px;
    width: 214px;

    margin-bottom: 20px;
}

.oc-colors ul li {
    display: inline-block;
    margin-right: 12px;
}

.oc-colors ul li .color {
    width: 9px;
    height: 9px;
    border: 0;
    box-sizing: border-box;
}

.oc-add-options {
    cursor: pointer;

    font-size: 12px;
    margin-top: 3px;
}

.oc-add-options img {
    transform: translate(2px, -2px);
}

.oc-price-location {
    display: grid;
    align-items: end;
}

.oc-price {
    color: #535353;

    font-size: 14px;
    font-weight: 500;
}

.oc-location {
    margin-left: 15px;

    font-size: 12px;
    font-weight: 500;
}

.oc-location img {
    margin-right: 5px;
}

.oc-check {
    position: relative;

    transform: translate(163px, -65px);

    width: 15px;
    height: 15px;
    
    border-radius: 50%;
    border: 1px solid silver;  
    background: #fff;

    cursor: pointer;
}

.oc-check-tick {
    transform: translate(3px, 4px);
    height: 8px;
}

.offers-price {
    color: #393939;
}

.offers-price h3 {
    margin: 8px 0;
}

.offers-price .over-price {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.offers-price .over-price h3 {
    font-size: 14px;
    font-weight: 600;
}

.offers-price .over-price span {
    font-size: 18px;
    font-weight: 600;
}

.offers-price .in-credit {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.offers-price .in-credit h3 {
    font-size: 12px;
    font-weight: 600;
}

.offers-price .in-credit span {
    font-size: 14px;
    font-weight: 600;
}


.offers-price .in-credit .op-small {
    font-size: 11px;
}

.offers-buttons {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
}

.offers-buy-but {
    background: #000;
}
