.main {
    height: calc(100% - 51px);

    display: grid;
    grid-template-areas: 'offer' 'footer';
    grid-template-rows: auto 1fr;
}

.header {
    display: grid;
    grid-template-areas: 'logo menu search';
    grid-template-columns: 1fr auto 1fr;
}

.logo {
    display: grid;
    align-items: center;
}

.logo div {
    display: flex;
    align-items: center;
}

.logo div img {
    height: 33px;
    transform: translateY(1px);
}

.logo div span {
    color: var(--text);

    font-size: 16px; 
    font-weight: 500;
}

.menu {
    display: flex;
    align-items: center;
    justify-content: center;

    color: var(--text);

    padding: 0;
}

.menu ul {
    margin: 0;
}

.menu ul li {
    display: inline;
    padding: 0 23px;

    font-size: 14px;

    cursor: pointer;
}

.menu-li {
    color: var(--text);
}

.menu-li-a {
    color: var(--text);
    text-decoration: none;
}

/* .menu ul li a {
    color: var(--text-color);
} */

.search-field {
    display: grid;
    justify-content: end;
}   

.search-input img {
    position: relative;
    left: 25px;
    top: 1px;
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;   
    color: var(--headers);
}

.search-field input {
    font-family: 'Montserrat', sans-serif;

    width: 200px;
    height: 32px;

    background: none;
    border: 1px solid silver;
    border-radius: 40px;

    outline: none;

    padding: 0 35px;
    box-sizing: border-box;
}

/* ////////////////////////////////////////// */
/*                   OFFER                    */
/* ////////////////////////////////////////// */

.offer {
    display: grid;
    grid-template-areas: 'props car price';
    grid-template-columns: 1fr 700px 1fr;
}

.car {
    /* TO */
    margin-top: 100px; 

    display: grid;
    grid-template-areas: 'photo' 'name' 'colors';
    grid-template-rows: 270px auto 1fr;
    grid-template-columns: 1fr;

    justify-content: center;
}

.car .car-name {
    font-size: 40px;
    font-weight: 700;

    color: var(--headers);

    width: 100%;
    text-align: center;

    letter-spacing: 2px;
}

.car-img {
    display: grid;
    justify-self: center;

    width: 615px;
    height: 100%;

    /* border: 1px solid silver; */
}

.color-picker {
    margin-top: 25px;

    display: grid;
    justify-self: center;
    justify-content: center;
}

.color-picker ul li {
    display: inline-block;

    padding: 0 11px;
    cursor: pointer;
}

.color {
    width: 15px;
    height: 15px;

    border-radius: 50%;
}

.color:first-child {
    border: 1px solid silver;
}

/* PROPERTIES */

.car-props {
    display: grid;
    align-items: center;
}

.car-props ul {
    list-style-type: none;
}

.car-props ul li {
    margin: 23px 0;
}

.car-props ul li h3 {
    margin: 0 0 9px 0;

    font-size: 18px;
    font-weight: 600;
    color: var(--headers);

    letter-spacing: 1px;
}

.car-props ul li span {
    color: var(--text);
    font-size: 14px;
}

/* Price */

.offer .car-price {
    display: grid;
    align-items: center;
    justify-content: end;
}

.offer .car-price ul li {
    list-style-type: none;
}

.offer .car-price ul li h3 {
    margin: 0 0 11px 0;

    font-size: 18px;
    font-weight: 600;
    letter-spacing: 1px;

    color: var(--headers);
}

.offer .car-price ul li {
    margin: 23px 0;
}

.offer .car-price .button {
    background: var(--buttons);
    color: var(--buttonsText);
    margin-top: 10px;
}

.offer .car-price .price-text {
    color: var(--text);
    font-size: 18px;
}

.car-price .price-text-small {
    color: var(--text);
}

/* ////////////////////////////////////////// */
/*                   FOOTER                    */
/* ////////////////////////////////////////// */

.footer {
    display: grid;

    grid-area: footer;
    grid-template-areas: 'course control question';
    grid-template-columns: 1fr auto 1fr;

    align-items: end;

    padding-bottom: 5px;
}

.course {
    color: var(--headers);
    font-size: 14px;
}

.course span {
    color: var(--text);
    font-weight: 500;
}

.control {
    display: grid;
    grid-template-columns: 1fr auto 1fr;

    align-items: center;
}

.control ul li {
    padding: 0 11px;

    font-size: 20px;
    font-weight: 600;

    display: inline;

    color: var(--color-1);

    cursor: pointer;
}

.control ul .active {
    color: var(--headers);
}

.control .arrow {
    margin: 0 10px;

    width: 40px;
    height: 10px;   

    background: url('/img/left-arrow.svg') no-repeat center;

    cursor: pointer;
}

.control .arrow-left {
    transform: translateY(-2px);
}

.control .arrow-right {
    transform: translateY(2px) rotate(180deg);
}

.send-question {
    color: var(--text);

    display: grid;

    grid-template-columns: auto auto;
    grid-column-gap: 12px;

    justify-content: end;
    align-items: center;

    cursor: pointer;

    font-size: 14px;
}

